<template>
    <div class="out-count-item">
        <md-field class="qw-field-content">
            <md-cell-item addon="点击编辑" arrow @click="editOutCountDetail" >
                <p slot="left" class="out-count-title">
                    <span>支出项 No.{{index + 1}}</span>
                    <br/>
                    <span>支出名称：{{ showOutCount.name }}</span>
                    <br>
                    <span>
                        金额：&yen; <md-amount :value="showOutCount.outCount" has-separator></md-amount>
                    </span>
                </p>
            </md-cell-item>
        </md-field>
        <md-popup v-model="isPopupShow" position="bottom">
            <md-popup-title-bar
                    title="支出项"
                    ok-text="确认"
                    cancel-text="取消"
                    @confirm="submitEdit()"
                    @cancel="cancelEdit()"
            ></md-popup-title-bar>
            <div>
                <md-field style="margin: 0">
                    <md-input-item
                            v-model="outCountForm.name"
                            title="名称"
                            placeholder="请输入名称"
                            clearable
                    ></md-input-item>
                    <md-input-item
                            ref="outcount"
                            v-model="outCountForm.outCount"
                            title="金额"
                            placeholder="请输入金额"
                            is-virtual-keyboard
                            clearable
                            @focus="onFakeInputFocus('outcount')"
                            @blur="onFakeInputBlur"
                    ></md-input-item>
                </md-field>
                <div class="popup-content-bottom"></div>
            </div>
        </md-popup>
    </div>
</template>

<script>
    export default {
        name: "outCountItem",
        props: {
            index: Number,
            name: {
                type: String,
                default: '无'
            },
            outCount: {
                type: Number,
                default: 0
            }
        },
        data() {
            return {
                outCountForm: {
                    name: '',
                    outCount: ''
                },
                showOutCount: {
                    name: '',
                    outCount: 0
                },
                isPopupShow: false
            }
        },
        methods: {
            // 输入金额高度上升
            onFakeInputFocus(ref) {
                if (!ref) return;
                document.querySelectorAll('.popup-content-bottom')[this.index].style.height = '4.28rem';
            },
            onFakeInputBlur() {
                document.querySelectorAll('.popup-content-bottom')[this.index].style.height = '0';
            },
            // 编辑支出项
            editOutCountDetail() {
                this.isPopupShow = true;
                this.outCountForm = {
                    outCount: this.outCount || '',
                    name: this.name
                };
            },
            cancelEdit() {
                this.isPopupShow = false;
            },
            submitEdit() {
                this.showOutCount.outCount = parseFloat(this.outCountForm.outCount || 0);
                this.showOutCount.name = this.outCountForm.name || '无';
                this.$emit('outCountConfirm', this.index, this.showOutCount);
                this.isPopupShow = false;
            }
        },
        created() {
            this.showOutCount = {
                outCount: this.outCount,
                name: this.name || '无'
            };
            this.outCountForm = {
                outCount: this.outCount || '',
                name: this.name
            };
        }
    }
</script>

<style lang="less" scoped>
    .qw-field-content {
        padding-top: 0;
        padding-bottom: 0;
    }
    .out-count-title {
        font-size: 0.32rem;
        &>span:first-child {
            font-size: 0.32rem;
            color: #80a7ff;
            line-height: 1.2;
        }
        span {
            font-weight: 500;
            font-size: 0.28rem;
            color: #666;
            line-height: 1.4;
        }
    }
    .popup-content-bottom {
        transition: all 0.3s;
    }
</style>
