<template>
    <div class="record">
        <md-field>
            <md-field-item
                    name="name"
                    title="记录日期"
                    arrow="arrow-right"
                    align="right"
                    :content="datePickerValue"
                    @click.native="isDatePickerShow = true">
            </md-field-item>
        </md-field>
        <md-date-picker
                ref="datePicker"
                v-model="isDatePickerShow"
                type="custom"
                title="选择记录日期"
                today-text="今天"
                :custom-types="['yyyy', 'MM','dd']"
                :default-date="currentDate"
                @confirm="onDatePickerConfirm"
        ></md-date-picker>
        <div class="loading" v-if="contentLoading">
            <md-activity-indicator :size="35" vertical class="md-activity-indicator-css">加载中...
            </md-activity-indicator>
        </div>
        <div v-else>
            <qw-divider>收入统计</qw-divider>
            <md-field class="qw-field-content">
                <md-input-item
                        v-model="inCountForm.wx"
                        title="微信"
                        placeholder="请输入金额"
                        is-virtual-keyboard
                        clearable
                        @focus="onFakeInputFocus('')"
                        @blur="onFakeInputBlur"
                ></md-input-item>
                <md-input-item
                        v-model="inCountForm.alipay"
                        title="支付宝"
                        placeholder="请输入金额"
                        is-virtual-keyboard
                        clearable
                        @focus="onFakeInputFocus('')"
                        @blur="onFakeInputBlur"
                ></md-input-item>
                <md-input-item
                        v-model="inCountForm.count"
                        title="现金"
                        placeholder="请输入金额"
                        is-virtual-keyboard
                        clearable
                        @focus="onFakeInputFocus('')"
                        @blur="onFakeInputBlur"
                ></md-input-item>
                <md-input-item
                        ref="input-meituan"
                        v-model="inCountForm.meituan"
                        title="美团"
                        placeholder="请输入金额"
                        is-virtual-keyboard
                        clearable
                        @focus="onFakeInputFocus('input-meituan')"
                        @blur="onFakeInputBlur"
                ></md-input-item>
                <md-input-item
                        ref="input-eleme"
                        v-model="inCountForm.eleme"
                        title="饿了么"
                        placeholder="请输入金额"
                        is-virtual-keyboard
                        clearable
                        @focus="onFakeInputFocus('input-eleme')"
                        @blur="onFakeInputBlur"
                ></md-input-item>
            </md-field>
            <total-count :value="incoutTotal"></total-count>
            <qw-divider>支出统计</qw-divider>
            <out-count-item v-for="(item, index) in outCountList"
                            :key="index"
                            :index="index"
                            :name="item.name"
                            :outCount="item.outCount"
                            @outCountConfirm="outCountConfirm">
            </out-count-item>
            <total-count :value="outcoutTotal"></total-count>
            <div class="button-section">
                <md-button type="primary" icon="edit" inline plain @click="addOutCountItem">添加支出项</md-button>
                <md-button type="primary" inline @click="save">
                    <md-activity-indicator
                            v-if="saveLoading"
                            class="md-activity-indicator-css"
                            type="carousel"
                            :size="15"
                            color="#fff"
                            text-color="#fff"
                    ></md-activity-indicator>
                    <span style="display: flex;" v-else><md-icon name="right" size="lg"></md-icon>保存</span>
                </md-button>
            </div>
        </div>
        <div class="bottom-white"></div>
    </div>
</template>

<script>
    import divider from '../components/divider'
    import outCountItem from '../components/outCountItem'
    import showTotal from '../components/showTotalCount'
    import {scrollAnimation} from '../utils/scrollAnimation'
    import {formatDate} from '../utils/formatDate'
    import {Toast} from 'mand-mobile'

    export default {
        name: "record",
        components: {
            [divider.name]: divider,
            [outCountItem.name]: outCountItem,
            [showTotal.name]: showTotal
        },
        data() {
            return {
                rootSize: '', // 根结点字体大小
                keyBoardHeight: '', //键盘高度
                navHeight: '',  // 顶部导航栏高度
                minDate: new Date('2013/9/9'),
                maxDate: new Date('2020/9/9'),
                currentDate: new Date(),
                isDatePickerShow: false,
                datePickerValue: '',
                inCountForm: {
                    wx: '',
                    alipay: '',
                    count: '',
                    meituan: '',
                    eleme: ''
                },
                outCountList: [{
                    'name': '',
                    'outCount': 0
                }],
                outcoutTotal: 0,
                saveLoading: false, // 保存按钮loading,
                contentLoading: true
            }
        },
        computed: {
            incoutTotal() {
                let res = 0;
                for (let key in this.inCountForm) {
                    if (key === 'date' || key === 'id') continue;
                    res += parseFloat(this.inCountForm[key] || 0);
                }
                return res;
            }
        },
        methods: {
            // 确认时间
            onDatePickerConfirm() {
                this.datePickerValue = this.$refs.datePicker.getFormatDate('yyyy-MM-dd');
                this.getCurrentData();
            },
            // 输入金额高度变化
            onFakeInputBlur() {
                document.querySelector('.bottom-white').style.height = '0';
                this.scrollInputBack && this.scrollInputBack()
            },
            // 输入金额高度变化
            onFakeInputFocus(ref) {
                if (!ref) return;
                document.querySelector('.bottom-white').style.height = '500px';
                let target = 0;
                const screenHeight = window.innerHeight;
                const _scrollHeight = this.keyBoardHeight + this.navHeight - screenHeight;
                target = this.$refs[ref].$el.offsetTop + _scrollHeight;
                scrollAnimation(target);
            },
            // 支出项填写完成
            outCountConfirm(index, data) {
                this.outCountList[index] = data;
                this.getOutcoutTotal();
            },
            // 计算支出总额
            getOutcoutTotal() {
                let res = 0;
                this.outCountList.forEach(item => {
                    res += parseFloat(item.outCount || 0)
                });
                this.outcoutTotal = res;
            },
            // 添加支出项
            addOutCountItem() {
                this.outCountList.push({
                    'name': '',
                    'outCount': 0
                });
            },
            formarObject(obj) {
                let res = {};
                for (let key in obj) {
                    res[key] = obj[key] || 0;
                }
                return res;
            },
            save() {
                if (this.saveLoading) {
                    return
                }
                this.saveLoading = true;
                let json = {
                    'date': this.datePickerValue,
                    'inCountList': this.formarObject(this.inCountForm),
                    'inCount': this.incoutTotal,
                    'outCountList': this.outCountList,
                    'outCount': this.outcoutTotal
                };
                this.$http.post('/api/save', json)
                    .then(res => {
                        if (res.data.code === 200) {
                            Toast.succeed('保存成功');
                            this.saveLoading = false;
                        } else {
                            Toast.failed(res.data.message);
                            this.saveLoading = false;
                        }
                    })
                    .catch(() => {
                        Toast.failed('保存失败，请稍后重试');
                        this.saveLoading = false;
                    })
            },
            // 获取当前日期数据
            getCurrentData() {
                this.contentLoading = true;
                const date = this.datePickerValue;
                this.$http.get('/api/getCurrentInfo?timestamp=' + date).then(res => {
                    if (res.data.code !== 200) {
                        Toast.failed(res.data.message);
                        this.contentLoading = false;
                    } else {
                        const data = res.data.data;
                        if (data) {
                            this.inCountForm = data.inCountList || {};
                            this.outCountList = data.outCountList || [
                                {
                                    'name': '',
                                    'outCount': 0
                                }
                            ];
                            this.getOutcoutTotal()
                        }
                        this.contentLoading = false;
                    }
                }).catch(() => {
                    Toast.failed('获取数据失败');
                    this.contentLoading = false;
                })
            }
        },
        mounted() {
            this.rootSize = parseFloat(document.getElementById('root').style.fontSize);
            this.keyBoardHeight = 1.07 * this.rootSize * 4;
            this.navHeight = 1.1 * this.rootSize;
            this.datePickerValue = formatDate(new Date());
            this.getCurrentData();
        }
    }
</script>

<style lang="less" scoped>
    .loading .md-activity-indicator-css {
        margin-top: 40%;
        font-size: 0.4rem;
    }
    .qw-field-content {
        padding-top: 0;
        padding-bottom: 0;
    }

    .button-section {
        float: right;
        margin: 20px 0;
        padding: 0 0.4rem;

        .md-button {
            float: left;
            margin-left: 0.4rem;
        }
    }
</style>
